import React, { Component } from 'react';
import Card from 'react-bootstrap/Card'
import { Button, Table } from 'react-bootstrap';
import Form from 'react-bootstrap/Form';
import Header from "../Header";

import ClipLoader from "react-spinners/ClipLoader";
import axios from "axios";
import { withRouter } from 'react-router-dom';

import moment from 'moment';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import Pagination from "@material-ui/lab/Pagination";
const dotenv = require('dotenv');
//import {CabTypes} from '../CabTypes';
class AddBookings extends Component {

    state = {
        userId: '', item: [], error: '', isLoading: false, loadingColor: '#ffffff', pageNo: 0, firstName: '', lastName: '', mobileNo: '', email: '', pickUp: '',
        destination: '', passengers: 0, language: '', totalAmount: 0, advanceAmount: 0, paidAmount: 0, bookingDate: new Date(), bookingDateSelected: new Date(),bookinglist:[], 
        cabType: '', cabTypes: [], errorListing: '',totalPage:1,rowCount:1,status:''
    };
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        this.setState({ isLoading: true });
        let userId = localStorage.getItem("userId");
        this.setState({ userId: userId });

        this.getCabs(userId);
        this.getBookings(userId,1);

    }

    async getCabs(userId) {
        try {
            this.setState({ cabId: 0 })
            let token = localStorage.getItem("token");
            let pageNo = this.state.pageNo + 1;
            const headers = { 'Authorization': `Bearer ${token}` };
            let urlData = "&userId=" + userId + "&pageId=" + pageNo;
            let cabData = await axios.get(process.env.REACT_APP_APIURL + 'admin/get_cabs?' + urlData, { headers });

            if (cabData.data.code == 200) {
                
                //console.log("cabData:" + JSON.stringify(cabData));
                this.setState({ cabTypes: cabData.data.data });
            } else {

            }
        } catch (err) {

            this.setState({ isOtpSent: 'N' });
            this.setState({ errorListing: err.response.data.message });
        }

        this.setState({ isLoading: false });

    }
    getBookings=async(userId,pageNo)=>{
        try {
            this.setState({ cabId: 0 })
            let token = localStorage.getItem("token");
            //let pageNo = this.state.pageNo + 1;
            const headers = { 'Authorization': `Bearer ${token}` };
            let urlData = "&userId=" + userId + "&pageId=" + pageNo;
            let bookingData = await axios.get(process.env.REACT_APP_APIURL + 'admin/get_cabbooking?' + urlData, { headers });

            if (bookingData.data.code == 200) {
                this.setState({totalPage:bookingData.data.totalPage});
                this.setState({rowCount:bookingData.data.rowCount});
                //console.log("bookinglist:" + JSON.stringify(bookingData));
                this.setState({ bookinglist: bookingData.data.data });
            } else {

            }
        } catch (err) {

            this.setState({ isOtpSent: 'N' });
            this.setState({ errorListing: err.response.data.message });
        }

        this.setState({ isLoading: false });

    }
    addBooking = async () => {
        let token = localStorage.getItem("token");

        const headers = { 'Authorization': `Bearer ${token}` };

        if (this.state.cabType == "" || this.state.cabType == null) {
            this.setState({ error: "Please enter car number" })
            return false;
        }
        if (this.state.firstName == "" || this.state.firstName == null) {
            this.setState({ error: "Please enter First Name" })
            return false;
        }
        if (this.state.lastName == "" || this.state.lastName == null) {
            this.setState({ error: "Please enter Last Name" })
            return false;
        }
        if (this.state.mobileNo == "" || this.state.mobileNo == null) {
            this.setState({ error: "Please enter Mobile No" })
            return false;
        }
        if (this.state.email == "" || this.state.email == null) {
            this.setState({ error: "Please enter Email" })
            return false;
        }
        if (this.state.pickUp == "" || this.state.pickUp == null) {
            this.setState({ error: "Please enter Pick Up Location" })
            return false;
        }
        if (this.state.destination == "" || this.state.destination == null) {
            this.setState({ error: "Please enter Destination" })
            return false;
        }
        if (this.state.passengers == "" || this.state.passengers == null) {
            this.setState({ error: "Please enter passengers" })
            return false;
        }
        if (this.state.language == "" || this.state.language == null) {
            this.setState({ error: "Please enter language" })
            return false;
        }
        if (this.state.totalAmount == "" || this.state.totalAmount == null) {
            this.setState({ error: "Please enter Total Amount" })
            return false;
        }
        if (this.state.advanceAmount == "" || this.state.advanceAmount == null) {
            this.setState({ error: "Please enter Advance Payment" })
            return false;
        }
        if (this.state.paidAmount == "" || this.state.paidAmount == null) {
            this.setState({ error: "Please enter Paid Amount" })
            return false;
        }
        if (this.state.bookingDate == "" || this.state.bookingDate == null) {
            this.setState({ error: "Please enter Booking Date" })
            return false;
        }

        let urlData = {
            firstName: this.state.firstName,
            cabType: this.state.cabType,
            lastName: this.state.lastName,
            mobileNo: this.state.mobileNo,
            email: this.state.email,
            pickUp: this.state.pickUp,
            destination: this.state.destination,
            passengers: this.state.passengers,
            language: this.state.language,
            totalAmount: this.state.totalAmount,
            advanceAmount: this.state.advanceAmount,
            paidAmount: this.state.paidAmount,
            bookingDate: this.state.bookingDateSelected,
            status:this.state.status
        }
        try {
            let token = localStorage.getItem("token");

            const headers = { 'Authorization': `Bearer ${token}`, 'Content-Type': 'application/json' };

            let cabData = await axios.post(process.env.REACT_APP_APIURL + 'admin/add_cabbooking', urlData, { headers });

            if (cabData.data.code == 200) {
                
                //this.setState({ item: cabData.data.data });
                this.setState({ error: 'Booking Added' });
                /*this.setState({ firstName: '' });
                this.setState({ lastName: '' });
                this.setState({ mobileNo: '' });
                this.setState({ email: '' });
                this.setState({ pickUp: '' });
                this.setState({ destination: '' });
                this.setState({ passengers: 0 });
                this.setState({ language: '' });
                this.setState({ totalAmount: 0 });
                this.setState({ advanceAmount: 0 });
                this.setState({ paidAmount: '' });
                this.setState({ status: '' });*/
                
                this.getBookings(this.state.userId,1);
            } else {
                this.setState({ error: 'Internal error' });
            }
        } catch (err) {

            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }

        this.setState({ isLoading: false });

    }

    onInputHandler(e) {
        const { name, value } = e.target;
        //this.setState({name: value});
    }
    setFirstName = (firstName) => {
        this.setState({ firstName: firstName.target.value });
    }
    setLastName = (lastName) => {
        this.setState({ lastName: lastName.target.value });
    }
    setMobileNo = (mobileNo) => {
        this.setState({ mobileNo: mobileNo.target.value });
    }
    setEmail = (email) => {
        this.setState({ email: email.target.value });
    }
    setPickUp = (pickUp) => {
        this.setState({ pickUp: pickUp.target.value });
    }
    setDestination = (destination) => {
        this.setState({ destination: destination.target.value });
    }
    setPassengers = (passengers) => {
        this.setState({ passengers: passengers.target.value });
    }
    setLanguage = (language) => {
        this.setState({ language: language.target.value });
    }
    setTotalAmount = (totalAmount) => {
        this.setState({ totalAmount: totalAmount.target.value });
    }
    setAdvanceAmount = (advanceAmount) => {
        this.setState({ advanceAmount: advanceAmount.target.value })
    }
    setPaidAmount = (paidAmount) => {
        this.setState({ paidAmount: paidAmount.target.value })
    }
    setStatus = (status) => {
        this.setState({ status: status.target.value })
    }
    setCabType = (cabType) => {
        this.setState({ cabType: cabType.target.value })
    }
    
    onpickTimeChange = (date) => {
        let picktime = date;
        let timeNow = moment(moment.now()).format("YYYY-MM-DD HH:mm:ss");
        let timeOnly = moment(date).format("H:mm:ss");
        let formattedDate = moment(date).format("YYYY-MM-DD HH:mm:ss");
        this.setState({ bookingDateSelected: formattedDate });
        //console.log("picktime==" + formattedDate);
        this.setState({ bookingDate: date });
    }
    deleteCabBooking = async (cabId) => {
        try {
            this.setState({ cabId: 0 })
            let token = localStorage.getItem("token");

            const headers = { 'Authorization': `Bearer ${token}` };
            let urlData = "&userId=" + this.state.userId + "&bookingId=" + cabId;

            let cabData = await axios.delete(process.env.REACT_APP_APIURL + 'admin/cabBooking?' + urlData, { headers });

            if (cabData.data.code == 200) {
                this.setState({ error: 'Booking deleted successfully' });
                this.getBookings(this.state.userId,1);
            } else {
                this.setState({ error: 'Something went wrong' });
            }
        } catch (err) {

            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }

        this.setState({ isLoading: false });
    }
    handlePageChange=async(event, value)=>{
        //this.setState({pageId:value});
        let userId=this.state.userId;
        let pageNo=value;
        this.setState({pageNo:pageNo});
        this.getBookings(userId,pageNo);
    }
    
    render() {
        const override = `
        display: block;
        margin: 0 auto;
        border-color: red;
        position: absolute;
        z-index: 9;
        margin: 0px auto;
        left: 40%;
        `;
        return (

            <div>
                <Header />
                <div>
                    <ClipLoader css={override} style={{ borderColor: 'red' }} color={this.state.loadingColor} loading={this.state.isLoading} size={150} >  </ClipLoader>
                </div>


                <div>
                    <section id="pricing" className="pricing">

                        <div className="container" data-aos="fade-up" style={{ width: '95%!important' }}>

                            <div className="row">
                                <div className="col-lg-12 col-md-12">

                                    <Card>
                                        <Card.Title className="bgColor" style={{ fontSize: 16, fontWeight: 'bold', borderRadius: "5px 5px 0px 0px", padding: 10, color: 'white' }}>Add Bookings

                                        </Card.Title>
                                        <Card.Body>
                                            <div style={{ color: 'red' }}>
                                                {this.state.error}
                                            </div>

                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>First Name <spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <Form.Control name="firstName" type="text" placeholder="First Name" value={this.state.firstName} onChange={this.setFirstName} />
                                                    </Form.Group>
                                                </div>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Last Name <spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <Form.Control name="lastName" type="text" placeholder="Last Name" value={this.state.lastName} onChange={this.setLastName} />
                                                    </Form.Group>
                                                </div>
                                            </div>
                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>mobileNo <spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <Form.Control name="mobileNo" type="text" placeholder="mobileNo" value={this.state.mobileNo} onChange={this.setMobileNo} />
                                                    </Form.Group>
                                                </div>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Email <spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <Form.Control name="email" type="text" placeholder="email" value={this.state.email} onChange={this.setEmail} />
                                                    </Form.Group>
                                                </div>
                                            </div>
                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>pickUp <spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <Form.Control name="pickUp" type="text" placeholder="pickUp" value={this.state.pickUp} onChange={this.setPickUp} />
                                                    </Form.Group>
                                                </div>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Destination <spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <Form.Control name="destination" type="text" placeholder="destination" value={this.state.destination} onChange={this.setDestination} />
                                                    </Form.Group>
                                                </div>
                                            </div>
                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                                <div>

                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Cab Type <spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <select className="form-control" value={this.state.cabType} onChange={this.setCabType}>
                                                            <option value="">Select Car Type</option>
                                                            {
                                                                this.state.cabTypes.map((object, i) => {
                                                                    return <option key={i+1} value={object.cabType}>{object.cabType}</option>
                                                                })
                                                            }
                                                        </select>
                                                    </Form.Group>
                                                </div>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Passengers <spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <Form.Control name="passengers" type="text" placeholder="Passengers" value={this.state.passengers} onChange={this.setPassengers} />
                                                    </Form.Group>
                                                </div>
                                            </div>

                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Booking Date</Form.Label>
                                                        <DatePicker popperPlacement="auto" showTimeSelect dateFormat="yyyy-MM-dd hh:mm aa" className="form-control"
                                                            selected={this.state.bookingDate}
                                                            minDate={new Date()}
                                                            onChange={date => this.onpickTimeChange(date)} />

                                                    </Form.Group>
                                                </div>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Luggage <spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <Form.Control name="language" type="text" placeholder="Luggage" value={this.state.language} onChange={this.setLanguage} />
                                                    </Form.Group>
                                                </div>
                                            </div>
                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Total Amount <spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <Form.Control name="totalAmount" type="text" placeholder="Total Amount" value={this.state.totalAmount} onChange={this.setTotalAmount} />
                                                    </Form.Group>
                                                </div>
                                                <div >
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Advance Amount<spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <Form.Control name="advanceAmount" type="text" placeholder="Advance Amount" value={this.state.advanceAmount} onChange={this.setAdvanceAmount} />
                                                    </Form.Group>
                                                </div>
                                            </div>
                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Pending Amount <spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <Form.Control name="paidAmount" type="text" placeholder="Pending Amount" value={this.state.paidAmount} onChange={this.setPaidAmount} />
                                                    </Form.Group>
                                                </div>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Status <spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <Form.Control name="paidAmount" type="text" placeholder="Confirm" value={this.state.status} onChange={this.setStatus} />
                                                    </Form.Group>
                                                </div>
                                            </div>

                                            <div className="col-12">
                                                <Button variant="primary" type="button" onClick={this.addBooking.bind(this)}>
                                                    Add Booking
                                                </Button>

                                            </div>
                                        </Card.Body>
                                    </Card>
                                </div>

                            </div>
                            <br />
                            <div className="row">
                                <div className="col-lg-12 col-md-12">

                                    <Card>
                                        <Card.Title className="bgColor" style={{ fontSize: 16, fontWeight: 'bold', borderRadius: "5px 5px 0px 0px", padding: 10, color: 'white' }}>Bookings </Card.Title>
                                        <Card.Body>
                                            <div style={{ color: 'red' }}>
                                                <div>{this.state.errorListing}</div>
                                                <Table striped bordered hover responsive>
                                                    <thead>
                                                        <tr>
                                                            <th>Sr.</th>
                                                            <th>First Name</th>
                                                            <th>Last Name</th>
                                                            <th>MobileNo</th>
                                                            <th>Email</th>
                                                            <th>Cab</th>
                                                            <th>Pick Up</th>
                                                            <th>Destination </th>
                                                            <th>Passengers</th>
                                                            <th>Luggage</th>
                                                            <th>Total Amount</th>
                                                            <th>Advance Amount</th>
                                                            <th>Pending Amount</th>
                                                            <th>Booking Date</th>
                                                            <th>Status</th>
                                                            <th>#</th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                        {
                                                            this.state.bookinglist.map((object, i) => {
                                                                return <tr key={i + 1}>
                                                                    <td>{i + 1}</td>
                                                                    <td>{object.firstName}</td>
                                                                    <td>{object.lastName}</td>
                                                                    <td>{object.mobileNo}</td>
                                                                    <td>{object.email}</td>
                                                                    <td>{object.cab}</td>
                                                                    <td>{object.pickUp}</td>
                                                                    <td>{object.destination}</td>
                                                                    <td>{object.passengers}</td>
                                                                    <td>{object.language}</td>
                                                                    <td>{object.totalAmount}</td>
                                                                    <td>{object.advanceAmount}</td>
                                                                    <td>{object.paidAmount}</td>
                                                                    <td>
                                                                        {object.bookingDate!=undefined?new Intl.DateTimeFormat('en-GB', { 
                                                                            month: 'long', 
                                                                            day: '2-digit',
                                                                            year: 'numeric', 
                                                                            hour:'numeric',
                                                                            minute:'numeric',
                                                                            hour12:true
                                                                        }).format(new Date(object.bookingDate)):null
                                                                        }
                                                                    </td>
                                                                    <td>{object.status}</td>
                                                                    <td>
                                                                        
                                                                        <Button style={{float:'right',marginRight:5}} variant="primary" type="button" onClick={this.deleteCabBooking.bind(this,object._id)}>
                                                                            <i className="fa fa-remove iconStyle" aria-hidden="true"></i>
                                                                        </Button>
                                                                        
                                                                    </td>
                                                                    

                                                                </tr>;
                                                            })
                                                        }

                                                    </tbody>
                                                </Table>
                                            </div>
                                            <Pagination
                                                className="paging"
                                                count={this.state.totalPage}
                                                page={this.state.pageId}
                                                defaultPage={this.state.pageId}
                                                siblingCount={2}
                                                boundaryCount={2}
                                                color="primary"
                                                showFirstButton showLastButton
                                                onChange={this.handlePageChange.bind()}
                                            />
                                        </Card.Body>
                                    </Card>
                                </div>

                            </div>

                        </div>

                    </section>


                </div>
            </div>
        );
    }
}

export default withRouter(AddBookings);